import { Resource, StudyRecord, PaymentRecord, Currency } from '../types';

// 
export class DataFormatter {
  // 
  static formatCurrency(amount: number, currency: Currency): string {
    const currencySymbols: Record<Currency, string> = {
      CNY: '',
      USD: '$',
      EUR: '',
      GBP: '',
      JPY: ''
    };

    const symbol = currencySymbols[currency] || '';
    return `${symbol}${amount.toFixed(2)}`;
  }

  // 
  static formatDuration(minutes: number): string {
    if (minutes < 60) {
      return `${minutes}`;
    }
    
    const hours = Math.floor(minutes / 60);
    const remainingMinutes = minutes % 60;
    
    if (remainingMinutes === 0) {
      return `${hours}`;
    }
    
    return `${hours}${remainingMinutes}`;
  }

  // 
  static formatDate(date: Date, format: 'short' | 'long' | 'relative' = 'short'): string {
    switch (format) {
      case 'short':
        return date.toLocaleDateString('zh-CN', {
          year: 'numeric',
          month: 'short',
          day: 'numeric'
        });
      
      case 'long':
        return date.toLocaleDateString('zh-CN', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        });
      
      case 'relative':
        const now = new Date();
        const diffMs = now.getTime() - date.getTime();
        const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
        const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
        const diffMinutes = Math.floor(diffMs / (1000 * 60));
        
        if (diffDays > 0) {
          return diffDays === 1 ? '' : `${diffDays}`;
        } else if (diffHours > 0) {
          return diffHours === 1 ? '1' : `${diffHours}`;
        } else if (diffMinutes > 0) {
          return diffMinutes === 1 ? '1' : `${diffMinutes}`;
        } else {
          return '';
        }
      
      default:
        return date.toLocaleDateString('zh-CN');
    }
  }

  // 
  static formatProgress(progress: number): string {
    return `${Math.round(progress)}%`;
  }

  // 
  static formatResourceStatus(status: Resource['status']): { text: string; color: string } {
    const statusMap = {
      not_started: { text: '', color: 'text-gray-500' },
      in_progress: { text: '', color: 'text-blue-500' },
      completed: { text: '', color: 'text-green-500' },
      abandoned: { text: '', color: 'text-red-500' }
    };
    
    return statusMap[status];
  }

  // 
  static formatResourceType(type: Resource['resourceType']): { text: string; icon: string } {
    const typeMap = {
      '': { text: '', icon: 'video' },
      '': { text: '', icon: 'users' },
      '': { text: '', icon: 'message-circle' },
      '': { text: '', icon: 'book' }
    };
    
    return typeMap[type];
  }

  // 
  static formatNumber(num: number): string {
    return num.toLocaleString('zh-CN');
  }

  // 
  static truncateText(text: string, maxLength: number = 100): string {
    if (text.length <= maxLength) {
      return text;
    }
    return text.substring(0, maxLength) + '...';
  }

  // 
  static getColorClass(text: string): string {
    const colors = [
      'bg-blue-100 text-blue-800',
      'bg-green-100 text-green-800',
      'bg-yellow-100 text-yellow-800',
      'bg-purple-100 text-purple-800',
      'bg-pink-100 text-pink-800',
      'bg-indigo-100 text-indigo-800',
      'bg-red-100 text-red-800',
      'bg-gray-100 text-gray-800'
    ];
    
    let hash = 0;
    for (let i = 0; i < text.length; i++) {
      hash = text.charCodeAt(i) + ((hash << 5) - hash);
    }
    
    const index = Math.abs(hash) % colors.length;
    return colors[index];
  }

  // 
  static formatFileSize(bytes: number): string {
    if (bytes === 0) return '0 B';
    
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB', 'TB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }
}