import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { useResourceStore, useProgressStore, usePaymentStore } from '@/stores';
import { useAuthStore } from '@/stores/authStore';
import { api } from '@/lib/api';
import { DataFormatter } from '@/utils';
import { toast } from 'sonner';
import {
  Settings as SettingsIcon,
  Download,
  Upload,
  Trash2,
  RefreshCw,
  Eye,
  EyeOff,
  Bell,
  BellOff,
  Palette,
  Globe,
  Shield,
  Database,
  FileText,
  Calendar,
  DollarSign,
  BookOpen,
  Clock,
  User,
  Save,
  RotateCcw
} from 'lucide-react';

interface SettingsState {
  theme: 'light' | 'dark' | 'auto';
  language: 'zh-CN' | 'en-US';
  notifications: {
    studyReminders: boolean;
    budgetAlerts: boolean;
    progressUpdates: boolean;
  };
  data: {
    autoBackup: boolean;
    backupInterval: number;
    exportFormat: 'json' | 'csv';
  };
  display: {
    showCompleted: boolean;
    showAbandoned: boolean;
    itemsPerPage: number;
    dateFormat: string;
  };
}

const defaultSettings: SettingsState = {
  theme: 'auto',
  language: 'zh-CN',
  notifications: {
    studyReminders: true,
    budgetAlerts: true,
    progressUpdates: true
  },
  data: {
    autoBackup: true,
    backupInterval: 7,
    exportFormat: 'json'
  },
  display: {
    showCompleted: true,
    showAbandoned: true,
    itemsPerPage: 10,
    dateFormat: 'YYYY-MM-DD'
  }
};

export default function Settings() {
  const { resources, clearAllData: clearResources } = useResourceStore();
  const { studyRecords, clearAllData: clearProgress } = useProgressStore();
  const { payments, clearAllData: clearPayments } = usePaymentStore();

  const [settings, setSettings] = useState<SettingsState>(defaultSettings);
  const [activeTab, setActiveTab] = useState<'general' | 'notifications' | 'data' | 'display' | 'account'>('general');
  const { user, fetchUser } = useAuthStore();
  const [nickNameInput, setNickNameInput] = useState<string>(user?.nick_name || '');
  const [pwdCurrent, setPwdCurrent] = useState('');
  const [pwdNew, setPwdNew] = useState('');
  const [savingProfile, setSavingProfile] = useState(false);
  const [changingPwd, setChangingPwd] = useState(false);
  const location = useLocation();
  const avatarInitial = ((user?.nick_name || user?.email || '').trim().charAt(0) || 'A').toUpperCase();
  const [isExporting, setIsExporting] = useState(false);
  const [isImporting, setIsImporting] = useState(false);

  // 
  const dataOverview = {
    resources: resources.length,
    studyRecords: studyRecords.length,
    payments: payments.length,
    totalSize: JSON.stringify({ resources, studyRecords, payments }).length
  };

  // 
  const saveSettings = (newSettings: Partial<SettingsState>) => {
    const updatedSettings = { ...settings, ...newSettings };
    setSettings(updatedSettings);
    localStorage.setItem('app-settings', JSON.stringify(updatedSettings));
    toast.success('');
  };

  // 
  const resetSettings = () => {
    if (confirm('')) {
      setSettings(defaultSettings);
      localStorage.setItem('app-settings', JSON.stringify(defaultSettings));
      toast.success('');
    }
  };

  // 
  const exportData = async () => {
    setIsExporting(true);
    try {
      const exportData = {
        resources,
        studyRecords,
        payments,
        exportDate: new Date().toISOString(),
        version: '1.0.0'
      };

      const dataStr = JSON.stringify(exportData, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      const url = URL.createObjectURL(dataBlob);
      
      const link = document.createElement('a');
      link.href = url;
      link.download = `knowledge-tracker-backup-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
      
      toast.success('');
    } catch (error) {
      toast.error('');
    } finally {
      setIsExporting(false);
    }
  };

  // 
  const importData = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    setIsImporting(true);
    const reader = new FileReader();
    
    reader.onload = (e) => {
      try {
        const importData = JSON.parse(e.target?.result as string);
        
        if (confirm('')) {
          //  store 
          // 
          toast.success('');
        }
      } catch (error) {
        toast.error('');
      } finally {
        setIsImporting(false);
        event.target.value = '';
      }
    };
    
    reader.readAsText(file);
  };

  // 
  const clearAllData = () => {
    if (confirm('')) {
      clearResources();
      clearProgress();
      clearPayments();
      toast.success('');
    }
  };

  // 
  const clearCache = () => {
    localStorage.clear();
    sessionStorage.clear();
    toast.success('');
    // 
    window.location.reload();
  };

  const tabs = [
    { id: 'general', label: '', icon: SettingsIcon },
    { id: 'notifications', label: '', icon: Bell },
    { id: 'data', label: '', icon: Database },
    { id: 'display', label: '', icon: Palette },
    { id: 'account', label: '', icon: User }
  ];

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const t = params.get('tab');
    if (t && ['general','notifications','data','display','account'].includes(t)) {
      setActiveTab(t as any);
    }
  }, [location.search]);

  useEffect(() => {
    setNickNameInput(user?.nick_name || '')
  }, [user?.nick_name, user?.email])

  return (
    <div className="space-y-6">
      {/*  */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900"></h1>
        <button
          onClick={resetSettings}
          className="inline-flex items-center px-3 py-2 text-sm text-gray-600 hover:text-gray-800 transition-colors"
        >
          <RotateCcw className="w-4 h-4 mr-2" />
          
        </button>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200">
        <div className="border-b border-gray-200">
          <nav className="flex space-x-8 px-6" aria-label="Tabs">
            {tabs.map((tab) => {
              const Icon = tab.icon;
              return (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id as any)}
                  className={`
                    flex items-center space-x-2 py-4 px-1 border-b-2 font-medium text-sm
                    ${activeTab === tab.id
                      ? 'border-blue-500 text-blue-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                    }
                  `}
                >
                  <Icon className="w-4 h-4" />
                  <span>{tab.label}</span>
                </button>
              );
            })}
          </nav>
        </div>

        <div className="p-6">
          {activeTab === 'account' && (
            <div className="space-y-6">
              <div className="bg-white rounded-lg border border-gray-200 p-6 space-y-4">
                <h3 className="text-lg font-medium text-gray-900"></h3>
                <div className="flex items-center space-x-3">
                  <div className="h-12 w-12 rounded-full bg-blue-600 text-white flex items-center justify-center text-lg font-semibold">
                    {avatarInitial}
                  </div>
                  <div className="text-gray-900 font-medium">{user?.nick_name || user?.email || ''}</div>
                </div>
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input value={user?.email || ''} readOnly className="w-full border border-gray-300 rounded-lg px-3 py-2 bg-gray-100 text-gray-600" />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input value={nickNameInput} onChange={(e)=>setNickNameInput(e.target.value)} className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                  </div>
                  <div className="flex justify-end">
                    <button onClick={async ()=>{ if(savingProfile) return; setSavingProfile(true); try { await api.updateUserProfile({ nick_name: nickNameInput }); await fetchUser(); toast.success(''); } catch { toast.error(''); } finally { setSavingProfile(false); } }} className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50">
                      <Save className="w-4 h-4 mr-2" />
                      
                    </button>
                  </div>
                </div>
              </div>

              <div className="bg-white rounded-lg border border-gray-200 p-6 space-y-4">
                <h3 className="text-lg font-medium text-gray-900"></h3>
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input type="password" value={pwdCurrent} onChange={(e)=>setPwdCurrent(e.target.value)} className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input type="password" value={pwdNew} onChange={(e)=>setPwdNew(e.target.value)} className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                  </div>
                  <div className="flex justify-end">
                    <button onClick={async ()=>{ if(changingPwd) return; setChangingPwd(true); try { await api.changePassword({ current_password: pwdCurrent, new_password: pwdNew }); setPwdCurrent(''); setPwdNew(''); toast.success(''); } catch { toast.error(''); } finally { setChangingPwd(false); } }} className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50">
                      <Save className="w-4 h-4 mr-2" />
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}
          {/*  */}
          {activeTab === 'general' && (
            <div className="space-y-6">
              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <select
                      value={settings.theme}
                      onChange={(e) => saveSettings({ theme: e.target.value as any })}
                      className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="auto"></option>
                      <option value="light"></option>
                      <option value="dark"></option>
                    </select>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <select
                      value={settings.language}
                      onChange={(e) => saveSettings({ language: e.target.value as any })}
                      className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="zh-CN"></option>
                      <option value="en-US">English</option>
                    </select>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button className="relative inline-flex h-6 w-11 items-center rounded-full bg-blue-600">
                      <span className="inline-block h-4 w-4 transform rounded-full bg-white transition translate-x-6" />
                    </button>
                  </div>
                  
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button className="relative inline-flex h-6 w-11 items-center rounded-full bg-blue-600">
                      <span className="inline-block h-4 w-4 transform rounded-full bg-white transition translate-x-6" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/*  */}
          {activeTab === 'notifications' && (
            <div className="space-y-6">
              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        notifications: {
                          ...settings.notifications,
                          studyReminders: !settings.notifications.studyReminders
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.notifications.studyReminders ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.notifications.studyReminders ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                  
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        notifications: {
                          ...settings.notifications,
                          budgetAlerts: !settings.notifications.budgetAlerts
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.notifications.budgetAlerts ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.notifications.budgetAlerts ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                  
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        notifications: {
                          ...settings.notifications,
                          progressUpdates: !settings.notifications.progressUpdates
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.notifications.progressUpdates ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.notifications.progressUpdates ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/*  */}
          {activeTab === 'data' && (
            <div className="space-y-6">
              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="bg-blue-50 p-4 rounded-lg">
                    <div className="flex items-center space-x-2">
                      <BookOpen className="w-5 h-5 text-blue-600" />
                      <span className="text-sm font-medium text-blue-900"></span>
                    </div>
                    <p className="text-2xl font-bold text-blue-600 mt-1">{dataOverview.resources}</p>
                  </div>
                  
                  <div className="bg-green-50 p-4 rounded-lg">
                    <div className="flex items-center space-x-2">
                      <Clock className="w-5 h-5 text-green-600" />
                      <span className="text-sm font-medium text-green-900"></span>
                    </div>
                    <p className="text-2xl font-bold text-green-600 mt-1">{dataOverview.studyRecords}</p>
                  </div>
                  
                  <div className="bg-purple-50 p-4 rounded-lg">
                    <div className="flex items-center space-x-2">
                      <DollarSign className="w-5 h-5 text-purple-600" />
                      <span className="text-sm font-medium text-purple-900"></span>
                    </div>
                    <p className="text-2xl font-bold text-purple-600 mt-1">{dataOverview.payments}</p>
                  </div>
                  
                  <div className="bg-orange-50 p-4 rounded-lg">
                    <div className="flex items-center space-x-2">
                      <Database className="w-5 h-5 text-orange-600" />
                      <span className="text-sm font-medium text-orange-900"></span>
                    </div>
                    <p className="text-sm font-bold text-orange-600 mt-1">
                      {DataFormatter.formatFileSize(dataOverview.totalSize)}
                    </p>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        data: {
                          ...settings.data,
                          autoBackup: !settings.data.autoBackup
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.data.autoBackup ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.data.autoBackup ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                  
                  {settings.data.autoBackup && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                      <select
                        value={settings.data.backupInterval}
                        onChange={(e) => saveSettings({
                          data: {
                            ...settings.data,
                            backupInterval: parseInt(e.target.value)
                          }
                        })}
                        className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      >
                        <option value={1}></option>
                        <option value={3}>3</option>
                        <option value={7}></option>
                        <option value={30}></option>
                      </select>
                    </div>
                  )}
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <select
                      value={settings.data.exportFormat}
                      onChange={(e) => saveSettings({
                        data: {
                          ...settings.data,
                          exportFormat: e.target.value as any
                        }
                      })}
                      className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="json">JSON</option>
                      <option value="csv">CSV</option>
                    </select>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-3">
                  <button
                    onClick={exportData}
                    disabled={isExporting}
                    className="w-full inline-flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 transition-colors"
                  >
                    <Download className="w-4 h-4 mr-2" />
                    {isExporting ? '...' : ''}
                  </button>
                  
                  <label className="w-full inline-flex items-center justify-center px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 cursor-pointer transition-colors">
                    <Upload className="w-4 h-4 mr-2" />
                    
                    <input
                      type="file"
                      accept=".json"
                      onChange={importData}
                      className="hidden"
                      disabled={isImporting}
                    />
                  </label>
                  
                  <button
                    onClick={clearAllData}
                    className="w-full inline-flex items-center justify-center px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                  >
                    <Trash2 className="w-4 h-4 mr-2" />
                    
                  </button>
                  
                  <button
                    onClick={clearCache}
                    className="w-full inline-flex items-center justify-center px-4 py-2 bg-orange-600 text-white rounded-lg hover:bg-orange-700 transition-colors"
                  >
                    <RefreshCw className="w-4 h-4 mr-2" />
                    
                  </button>
                </div>
              </div>
            </div>
          )}

          {/*  */}
          {activeTab === 'display' && (
            <div className="space-y-6">
              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        display: {
                          ...settings.display,
                          showCompleted: !settings.display.showCompleted
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.display.showCompleted ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.display.showCompleted ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                  
                  <div className="flex items-center justify-between">
                    <div>
                      <label className="text-sm font-medium text-gray-700"></label>
                      <p className="text-xs text-gray-500"></p>
                    </div>
                    <button
                      onClick={() => saveSettings({
                        display: {
                          ...settings.display,
                          showAbandoned: !settings.display.showAbandoned
                        }
                      })}
                      className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                        settings.display.showAbandoned ? 'bg-blue-600' : 'bg-gray-200'
                      }`}
                    >
                      <span className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
                        settings.display.showAbandoned ? 'translate-x-6' : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                  <select
                    value={settings.display.itemsPerPage}
                    onChange={(e) => saveSettings({
                      display: {
                        ...settings.display,
                        itemsPerPage: parseInt(e.target.value)
                      }
                    })}
                    className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    <option value={5}>5 </option>
                    <option value={10}>10 </option>
                    <option value={20}>20 </option>
                    <option value={50}>50 </option>
                  </select>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-900 mb-4"></h3>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                  <select
                    value={settings.display.dateFormat}
                    onChange={(e) => saveSettings({
                      display: {
                        ...settings.display,
                        dateFormat: e.target.value
                      }
                    })}
                    className="w-full border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    <option value="YYYY-MM-DD">2024-01-01</option>
                    <option value="DD/MM/YYYY">01/01/2024</option>
                    <option value="MM/DD/YYYY">01/01/2024</option>
                    <option value="DD-MM-YYYY">01-01-2024</option>
                  </select>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/*  */}
      <div className="flex justify-end">
        <button
          onClick={() => toast.success('')}
          className="inline-flex items-center px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
          <Save className="w-4 h-4 mr-2" />
          
        </button>
      </div>
    </div>
  );
}