import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuthStore } from '@/stores/authStore'
import { toast } from 'sonner'
import { api } from '@/lib/api'
import { BookOpen, Github, User, Mail, Lock, UserPlus } from 'lucide-react'

export default function Login() {
  const navigate = useNavigate()
  const { login } = useAuthStore()
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [nickName, setNickName] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [info, setInfo] = useState<string | null>(null)
  const [isRegisterMode, setIsRegisterMode] = useState(false)

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    setInfo(null)
    try {
      if (isRegisterMode) {
        await onRegister()
      } else {
        try {
          await login(email, password)
        } catch (err: any) {
          const m = String(err?.message || '')
          if (m.includes('') || m.includes('UsernameNotFound') || m.includes('NOT_LOGIN') || m.includes('no_auth')) {
            await api.userRegister({ email, password, nick_name: nickName || email.split('@')[0] })
            await login(email, password)
          } else {
            throw err
          }
        }
        navigate('/')
      }
    } catch (e: any) {
      // 
      if (e.message?.includes('Failed to fetch')) {
        setError('')
        toast.error('')
      } else if (e.message?.includes('NetworkError')) {
        setError('')
        toast.error('')
      } else {
        const msg = e.message || (isRegisterMode ? '' : '')
        setError(msg)
        toast.error(msg)
      }
    } finally {
      setLoading(false)
    }
  }

  const onRegister = async () => {
    setLoading(true)
    setError(null)
    setInfo(null)
    try {
      await api.userRegister({ email, password, nick_name: nickName })
      setInfo('')
      setIsRegisterMode(false)
    } catch (e: any) {
      // 
      if (e.message?.includes('Failed to fetch')) {
        setError('')
        toast.error('')
      } else if (e.message?.includes('NetworkError')) {
        setError('')
        toast.error('')
      } else {
        const msg = e.message || ''
        setError(msg)
        toast.error(msg)
      }
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50 flex items-center justify-center p-4">
      <div className="max-w-md w-full">
        {/*  */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-blue-100 rounded-full mb-4">
            <BookOpen className="w-8 h-8 text-blue-600" />
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2"></h1>
          <p className="text-gray-600">{isRegisterMode ? '' : ''}</p>
        </div>

        {/*  */}
        <div className="bg-white rounded-2xl shadow-xl border border-gray-100 p-8">
          <form onSubmit={onSubmit} className="space-y-6">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                <Mail className="inline w-4 h-4 mr-2" />
                
              </label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                placeholder=""
                required
              />
            </div>

            {/*  */}
            {isRegisterMode && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  <User className="inline w-4 h-4 mr-2" />
                  
                </label>
                <input
                  type="text"
                  value={nickName}
                  onChange={(e) => setNickName(e.target.value)}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                  placeholder=""
                />
              </div>
            )}

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                <Lock className="inline w-4 h-4 mr-2" />
                
              </label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                placeholder=""
                required
              />
            </div>

            {/*  */}
            {error && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                <p className="text-red-600 text-sm">{error}</p>
              </div>
            )}
            {info && (
              <div className="bg-green-50 border border-green-200 rounded-lg p-3">
                <p className="text-green-600 text-sm">{info}</p>
              </div>
            )}

            {/*  */}
            <button
              type="submit"
              disabled={loading}
              className="w-full px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 font-medium"
            >
              {loading ? (
                <span className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  {isRegisterMode ? '...' : '...'}
                </span>
              ) : (
                isRegisterMode ? '' : ''
              )}
            </button>

            {/* GitHub  */}
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300" />
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white text-gray-500"></span>
              </div>
            </div>

            <button
              type="button"
              onClick={async () => {
                try {
                  const url = await api.oauthAuthorize('github')
                  window.location.href = url
                } catch (e) {
                  setError('GitHub ')
                }
              }}
              className="w-full px-6 py-3 bg-gray-900 text-white rounded-lg hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-gray-500 focus:ring-offset-2 transition-all duration-200 font-medium flex items-center justify-center space-x-2"
            >
              <Github className="w-5 h-5" />
              <span> GitHub </span>
            </button>
          </form>

          {/* / */}
          <div className="mt-6 text-center">
            <p className="text-sm text-gray-600">
              {isRegisterMode ? '' : ''}
              <button
                type="button"
                onClick={() => {
                  setIsRegisterMode(!isRegisterMode)
                  setError(null)
                  setInfo(null)
                }}
                className="ml-1 text-blue-600 hover:text-blue-500 font-medium transition-colors duration-200"
              >
                {isRegisterMode ? '' : ''}
              </button>
            </p>
          </div>
        </div>

        {/*  */}
        <div className="mt-6 text-center">
          <p className="text-xs text-gray-500">
            
          </p>
        </div>
      </div>
    </div>
  )
}